import { Thread } from './Thread';
export class Tablet {
    constructor(sDirection, holes, threads) {
        this.sDirection = sDirection;
        this.holes = holes;
        this.threads = threads;
    }
    toString() {
        return `${(this.sDirection ? '1' : '0')}` + this.threads.reduce((previous, current) => `${previous}${current.toString()}`, '');
    }
    static fromString(tablet) {
        return new Tablet(tablet[0] === '1', 4, tablet.substring(2).split('#').map(colorCode => {
            return Thread.fromString(colorCode);
        }));
    }
}
