export class Thread {
    constructor(color, empty = false) {
        this.empty = false;
        this.color = color;
        this.empty = empty;
    }
    toString() {
        return this.empty ? '#XXXXXX' : `${this.color}`;
    }
    static fromString(colorCode) {
        if (colorCode === 'XXXXXX') {
            return new Thread('#FFFFFF', true);
        }
        return new Thread(`#${colorCode}`);
    }
}
